const Dashboard = React.lazy(() => import('./views/AdminDashboard/Dashboard/Dashboard'));
const Doctors = React.lazy(() => import('./views/AdminDashboard/Doctors/Doctors'));
const DoctorsDetails = React.lazy(() => import('./views/AdminDashboard/Doctors/DoctorsDetails'));
const DoctorsPayment = React.lazy(() => import('./views/AdminDashboard/Doctors/Payment/Payment'));
const Users = React.lazy(() => import('./views/AdminDashboard/Users/Users'));
const User = React.lazy(() => import('./views/AdminDashboard/Users/User'));
const PatientPayment = React.lazy(() => import('./views/AdminDashboard/Users/Payment/Payment'));
const Category = React.lazy(() => import('./views/AdminDashboard/Category/Category'));
const SubCategory = React.lazy(() => import('./views/AdminDashboard/SubCategory/SubCategory'));
const ChangePassword  = React.lazy(() => import('./views/AdminDashboard/ChangePassword/ChangePassword'));
const TemplateList  = React.lazy(() => import('./views/AdminDashboard/TemplateBuilder/Template'));
const TemplateBuilder  = React.lazy(() => import('./views/AdminDashboard/TemplateBuilder/TemplateBuilderPage'));
const PreviewTemplatePage  = React.lazy(() => import('./views/AdminDashboard/TemplateBuilder/PreviewTemplatePage'));
const Employee = React.lazy(() => import('./views/AdminDashboard/Doctors/Employee/Employee'));
const Inspection = React.lazy(() => import('./views/AdminDashboard/Inspection/InspectionLists'));
const InspectionFeedBack = React.lazy(() => import('./views/AdminDashboard/Inspection/InspectionFeedBack'));
const AssignInspection = React.lazy(() => import('./views/AdminDashboard/Inspection/AssignInspection'));
const FeedBackPreviewPage = React.lazy(() => import('./views/AdminDashboard/Inspection/FeedBackPreviewPage'));
const Store = React.lazy(() => import('./views/AdminDashboard/Doctors/Store/Store'));
const ActionLists = React.lazy(() => import('./views/AdminDashboard/Inspection/ActionLists'));
const Subscription = React.lazy(() => import('./views/AdminDashboard/Subscription/Subscription'));
const SubscriberList = React.lazy(() => import('./views/AdminDashboard/Reports/Subscriber'));
const LeaderBoard = React.lazy(() => import('./views/AdminDashboard/Reports/LeaderBoard'));
const Profile = React.lazy(() => import('./views/AdminDashboard/Profile/Profile'));
const TemplateCategory  = React.lazy(() => import('./views/AdminDashboard/TemplateBuilder/Category/Category'));
/*const Assignment = React.lazy(() => import('./views/AdminDashboard/Assignment/Assignment'));
const AssignmentDetails = React.lazy(() => import('./views/AdminDashboard/Assignment/AssignmentDetails'));*/
const ClinicalTrial = React.lazy(() => import('./views/AdminDashboard/ClinicalTrial/ClinicalTrial'));
const ClinicalTrialDetails = React.lazy(() => import('./views/AdminDashboard/ClinicalTrial/ClinicalTrialDetails'));
const ClinicalTrialProfile = React.lazy(() => import('./views/AdminDashboard/ClinicalTrial/ClinicalTrialProfile'));
const UnApprovedUser  = React.lazy(() => import('./views/AdminDashboard/Users/UnApprovedUser'));
const Specialization  = React.lazy(() => import('./views/AdminDashboard/Specialization/Specialization'));
const SuperSpecialization  = React.lazy(() => import('./views/AdminDashboard/Specialization/SuperSpecialization'));
const Sponsors  = React.lazy(() => import('./views/AdminDashboard/Sponsors/Sponsors'));

const adminRoutes = [
  { path: '/admin/', exact: true, name: 'Home' },
  { path: '/admin/dashboard', name: 'Dashboard', component: Dashboard },  
  { path: '/admin/users', exact: true,  name: 'Patient', component: Users },  
  { path: '/admin/profile', exact: true,  name: 'Profile', component: Profile }, 
  { path: '/admin/users/payment', exact: true, name: 'Patient Payment', component: PatientPayment },
  { path: '/admin/users/new', exact: true, name: 'New Patient ', component: UnApprovedUser },
  { path: '/admin/users/:profileId', exact: true, name: 'Patient Details', component: User },
  { path: '/admin/manage-practice/practice', exact: true,  name: 'Practice', component: Category },
  { path: '/admin/manage-practice/sub-practice', exact: true,  name: 'Sub Practice', component: SubCategory },
  { path: '/admin/manage-doctors/doctors', exact: true,  name: 'Doctors', component: Doctors },
  { path: '/admin/manage-doctors/payment', exact: true,  name: 'Doctors Payment', component: DoctorsPayment },
  { path: '/admin/manage-doctors/doctors/:doctorsId', exact: true,  name: 'Doctors Details', component: DoctorsDetails },
  { path: '/admin/manage-doctors/employee', exact: true,  name: 'Employee', component: Employee },
  { path: '/admin/manage-doctors/employee/:doctorsId', exact: true,  name: 'Employee', component: Employee },
  { path: '/admin/manage-doctors/store', exact: true,  name: 'Store', component: Store },
  { path: '/admin/manage-doctors/store/:doctorsId', exact: true,  name: 'Store', component: Store },
  { path: '/admin/change-password', exact: true,  name: 'Change Password', component: ChangePassword },
  { path: '/admin/manage-template/template', exact: true,  name: 'Template Library', component: TemplateList },
  { path: '/admin/manage-template/create-template', exact: true,  name: 'Create Template', component: TemplateBuilder },
  { path: '/admin/manage-template/template-category', exact: true,  name: 'Template Category', component: TemplateCategory },
  { path: '/admin/manage-template/create-template/:templateId', exact: true,  name: 'Create Template', component: TemplateBuilder },
  { path: '/admin/manage-template/preview/:inspectionId', exact: true,  name: 'Preview Template', component: PreviewTemplatePage },
  { path: '/admin/manage-research/research', exact: true,  name: 'Research', component: Inspection }, 
  { path: '/admin/manage-research/assign-research', exact: true,  name: 'Assign Research', component: AssignInspection },
  { path: '/admin/manage-research/assign-research/:inspectionId', exact: true,  name: 'Assign Research', component: AssignInspection },
  /*{ path: '/admin/manage-trial/clinical-trial', exact: true,  name: 'Clinical Trial', component: Assignment },
  { path: '/admin/manage-trial/clinical-trial/:assignmentId', exact: true,  name: 'Trial Details', component: AssignmentDetails },*/
  { path: '/admin/manage-inspection/inspection/:inspectionId', exact: true,  name: 'Inspection Feedback', component: InspectionFeedBack },
  { path: '/admin/manage-inspection/inspection/:inspectionId/:feedBackId', exact: true,  name: 'Feedback Preview', component: FeedBackPreviewPage },
  { path: '/admin/action', exact: true,  name: 'Action', component: ActionLists },
  { path: '/admin/subscription', exact: true,  name: 'Subscription', component: Subscription },
  { path: '/admin/reports/subscriber-list', exact: true,  name: 'Subscriber List', component: SubscriberList },
  { path: '/admin/reports/leaderboard', exact: true,  name: 'LeaderBoard', component: LeaderBoard },
  { path: '/admin/manage-trial/clinical-trial', exact: true, name: 'Clinical Trial', component: ClinicalTrialDetails },
  { path: '/admin/manage-trial/create', exact: true, name: 'Clinical Trial', component: ClinicalTrial }, 
  { path: '/admin/manage-trial/clinical-trial/:clinicalTrialId', exact: true,  name: 'Clinical Trial Details', component: ClinicalTrialProfile },
  { path: '/admin/clinical-trial/detail', exact: true, name: 'Clinical Trial', component: ClinicalTrialDetails }, 
  { path: '/admin/clinical-trial/profile', exact: true, name: 'Clinical Trial', component: ClinicalTrialProfile }, 
  { path: '/admin/manage-practice/master-specialization', exact: true, name: 'Specialization', component: Specialization },
  { path: '/admin/manage-practice/super-specialization', exact: true, name: 'Super Specialization', component: SuperSpecialization },
  { path: '/admin/master-sponsors', exact: true, name: 'Master Sponsors', component: Sponsors }, 

];

export default adminRoutes;
